import styled from "styled-components";
import Title from "../utils/reUseableComponents/Title";
import useCalculatePrice from "../utils/customHooks/useCalculatePrice";

const OrderSummary = () => {
  const { productPrice, shippingCost, tax, total } = useCalculatePrice();

  return (
    <Container>
      <div className="content">
        <Title text="Order Summary" style={{}} />
        <br />
        <dl className="sub_total">
          <dt>Product price</dt>
          <dd> ${productPrice}</dd>
        </dl>

        <dl className="shipping">
          <dt>Shipping cost</dt>
          <dd> ${shippingCost}</dd>
        </dl>
        <dl className="shipping">
          <dt>Tax</dt>
          <dd> ${tax}</dd>
        </dl>
        <div className="hr"></div>

        <dl className="total">
          <dt>Total</dt>
          <dd> ${total}</dd>
        </dl>
      </div>
    </Container>
  );
};

export default OrderSummary;

const Container = styled.div`
  max-width: 35rem;

  @media screen and (max-width: 768px) {
    /* margin: 0; */
  }

  .content {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin-top: 3.3rem;
    padding: 2rem;
    box-shadow: var(--shadow-3);
    border-radius: 0.5rem;

    dd,
    dt {
      font-size: 1.4rem;
      color: #000;
      font-weight: 400;
    }
    .sub_total {
      display: flex;
      justify-content: space-between;
    }
    .shipping {
      display: flex;
      justify-content: space-between;
    }
    .total {
      display: flex;
      justify-content: space-between;
      align-items: center;

      dd,
      dt {
        font-size: 1.4rem;
        color: #000;
        font-weight: 600;
      }
      dd {
        font-size: 2rem;
      }
    }
    .hr {
      border: 1px solid #f2f2f2;
      margin: 2rem 0;
    }
    .buy_now {
      width: 100%;
      border: none;
      background: tomato;
      padding: 1rem 0;
      border-radius: 0.5rem;
      font-size: 1.6rem;
      color: #fff;
      cursor: pointer;
      margin: 2rem 0 1rem;
    }
  }
  @media screen and (max-width: 768px) {
    width: 100%;
    padding-right: 0.5rem;
  }
`;
