import React from "react";
import styled from "styled-components";
import PageContainer from "../utils/PageContainer";
import OrderSummary from "./OrderSummary";
import OrderDetails from "./OrderDetails";
import { useGetMyCartListQuery } from "../../redux/api/cart/cartAPI";
import EmptyItems from "../utils/EmptyItems";
import Error from "../utils/fetchUtils/Error";

const Order = () => {
  // get my cart list
  const { isSuccess, data, isError, error } = useGetMyCartListQuery();

  return (
    <PageContainer>
      <OrderContainer>
        {isError && (
          <>
            {error.status === 404 ? (
              <EmptyItems
                text={error.data?.message || "Your cart is empty."}
                link={"/products"}
                btnText={"Add Product"}
              />
            ) : (
              <Error text={error.data?.message} style={{ padding: "5rem 0" }} />
            )}
          </>
        )}

        {isSuccess && data?.carts?.length && (
          <Content>
            <OrderDetails />
            <OrderSummary />
          </Content>
        )}
      </OrderContainer>
    </PageContainer>
  );
};

export default Order;

const OrderContainer = styled.div`
  width: 100%;
  max-width: 1440px;
`;

const Content = styled.div`
  display: flex;
  justify-content: space-between;
  gap: 5rem;
  padding: 2rem 0;

  @media screen and (max-width: 768px) {
    flex-direction: column;
    padding-bottom: 5rem;
  }
`;
